import { Component, OnInit } from '@angular/core';
import { JsonFormsAngularService } from '@jsonforms/angular';
import { OwnPropsOfRenderer } from '@jsonforms/core';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';

@Component({
  selector: 'ssr-grid-layout',
  templateUrl: './grid-layout.component.html',
  styleUrls: ['./grid-layout.component.scss'],
})
export class GridLayoutComponent extends BaseJsonformsAbstractControlComponent
implements OnInit 
{
  display_type: any;

  constructor(jsonformsService: JsonFormsAngularService) {
    super(jsonformsService);
    console.log("fn7 helika constructor GridLayoutComponent")
  }

  override ngOnInit(): void {
    super.ngOnInit();
  }

  public override mapAdditionalProps(props: any) {
    this.props = props;
    this.display_type = this.jsonFormsService.getState().jsonforms.core?.uischema.options?.['IsUpsert'] ? 'edit' : 'view';
  }



  getProps(index: number, child: any): OwnPropsOfRenderer {

    const result = {
      schema: this.scopedSchema,
      // path: Paths.compose(this.propsPath, `${index}`),
      path: this.propsPath,
      uischema: child,
    };
    return result;
  }

  trackByFn(index: number) {
    return index;
  }
}
