import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  Renderer2,
  ViewChild,
} from '@angular/core';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { JsonFormsAngularService } from '@jsonforms/angular';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'ssr-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss'],
})
export class ButtonComponent extends BaseJsonformsAbstractControlComponent {
  currentLocale: string;
  display_type: string = 'view';
  buttonType: 'mat-raised-button';
  labelName: any;
  isLoading: any = false;
  loaderColor: string = '#2d2b29';

  @ViewChild('button', { static: true }) button: ElementRef;

  constructor(
    private service: JsonFormsAngularService,
    private renderer: Renderer2,
    private cdr: ChangeDetectorRef
  ) {
    super(service);
  }

  override ngOnInit(): void {
    super.ngOnInit();
    if (this.props.uischema.options.loadingText) {
      const buttonProgress$ = new BehaviorSubject(false);
      this.props.uischema.options['buttonProgress$'] = buttonProgress$;
      let btnSubscribe = buttonProgress$.subscribe((buttonProgressValue) => {
        if (buttonProgressValue) {
          console.log('triggeredd...');
          this.isLoading = true;
          this.loaderColor =
            this.props.uischema.options.loaderColor || this.loaderColor;
          this.labelName =
            this.props.uischema.options.loadingText || this.labelName;
          this.cdr.detectChanges();
        } else {
          this.isLoading = false;
          this.labelName = this.props.id
            ? this.props.data || this.props.label
            : this.props.label;
          this.cdr.detectChanges();
        }
      });
    }
  }

  public override mapAdditionalProps(props: any) {
    this.props = props;
    this.labelName = this.props.id
      ? this.props.data || this.props.label
      : this.props.label;
    this.display_type = props.uischema.options.display_type;
    this.updateButtonStyles();
    // console.log('button props:', this.props);
  }

  updateButtonStyles() {
    const buttonElement = this.button?.nativeElement;

    if (buttonElement)
      this.renderer.setAttribute(buttonElement, 'mat-raised-button', '');
  }

  getClassName(position: any) {
    // possbie postions top, bottom, left, right
    let className = 'flex-col';
    if (!position) {
      return className;
    } else if (position === 'top') {
      className = 'flex flex-col';
    } else if (position == 'bottom') {
      className = 'flex flex-col-reverse';
    } else if (position == 'left') {
      className = 'flex flex-row items-center gap-2';
    } else if (position == 'right') {
      className = 'flex flex-row-reverse items-center gap-2';
    } else {
      className = position;
    }

    return className;
  }
}
