import { Component } from '@angular/core';
import {
  Fn7SDKService,
  WorkFlowInput,
  WorkflowEngineService,
} from '@app/fn7-common-lib';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { get } from 'lodash';
import { SnackbarService } from '@app/fn7-base-lib';
import { ActivatedRoute, Router } from '@angular/router';
import { DialogReferenceComponent } from '@app/standalone-modules';
import { JsonFormsAngularService } from '@jsonforms/angular';
import { Paths } from '@jsonforms/core';

@Component({
  selector: 'ssr-control',
  templateUrl: './control.component.html',
  styleUrls: ['./control.component.scss'],
})
export class ControlComponent extends BaseJsonformsAbstractControlComponent {
  uischematorender: any;
  JsonFormsMode = 'View';
  isItAdmin = false;
  IsDevMode: false;
  CurretnIterationdata: any;
  showControl = true;
  work: any;
  jsonContext: any;
  icon: any = undefined;

  constructor(
    // private fn7Sdk: Fn7SDKService
    private snackBar: SnackbarService,
    private route: ActivatedRoute,
    private router: Router,
    jsonformsService: JsonFormsAngularService,
    private fn7Sdk: Fn7SDKService
  ) {
    super(jsonformsService);
    console.log('fn7 helika constructor ControlComponent');
  }

  override ngOnInit(): void {
    super.ngOnInit();
    // if (window.location.href.indexOf('dynamic') > -1) {
    //   this.isItAdmin = true;
    //   // this.cdr.markForCheck();
    // }
  }
  getPath(index: number): any {
    return Paths.compose(this.propsPath, `${index}`);
  }
  override getEventValue = (event: any) => {
    return event.target.value;
  };

  public override mapAdditionalProps(props: any) {
    // console.log(props);
    // console.log(this)
    this.props = props;
    this.icon = this.props.uischema.options?.icon;
    if (this.uischema?.options?.['iconFunction']) {
      let rootData = this.jsonFormsService.getState().jsonforms.core?.data;
      let iconFunction = this.uischema?.options?.['iconFunction'];
      const iconFn: any = new Function('params , props', iconFunction);
      this.icon = iconFn(rootData, props);
    }
    this.uischematorender = { ...this.uischema };
    // remove  type: 'Control' from uischema
    this.uischematorender.type = '';
    // console.table(this.uischematorender)

    // chakc for rules and hide the field
    if (this.uischema.rule) {
    }

    this.JsonFormsMode =
      this.jsonFormsService.getState().jsonforms.core?.uischema.options?.[
        'mode'
      ];
    this.IsDevMode =
      this.jsonFormsService.getState().jsonforms.core?.uischema.options?.[
        'devMode'
      ];
    // console.log(this.jsonFormsService.getState().jsonforms.core?.uischema)

    if (this.IsDevMode) {
      alert('Dev Mode');
    }
    // this.cdr.detectChanges();

    // reset memory
    // if (this.getProps.cache) {
    //   this.getProps.cache.clear(); // Clear the cache
    // }

    // this.cdr.markForCheck();
  }
  get isTypeAction(): boolean {
    return (
      this.props.uischema.options?.IsTypeActon ||
      this.props.uischema.options?.IsTypeAction
    );
  }
  deleteField() {
    console.log('deleteField');
  }

  trackByFn(index: number) {
    return index;
  }

  getClassName(position: any) {
    // possbie postions top, bottom, left, right
    let className = 'flex-col';
    if (!position) {
      return className;
    } else if (position === 'top') {
      className = 'flex-col';
    } else if (position == 'bottom') {
      className = 'flex-col-reverse';
    } else if (position == 'left') {
      className = 'flex-row items-center';
    } else if (position == 'right') {
      className = 'flex-row-reverse items-center';
    } else {
      className = position;
    }

    return className;
  }

  // check rules  if jsonform is hidden
  get checkRules() {
    let rules: any = this.uischema.rule;
    let status = false;
    if (!rules) {
      return false;
    }
    rules.map((item: any) => {
      let rule = item.rule;
      let ruleValue = item.ruleValue;
      let data = this.jsonFormsService.getState().jsonforms.core?.data;
      let value = get(data, rule);
      console.log('checkRules', value, ruleValue);
      if (value == ruleValue) {
        status = true;
      }
    });
    return status;
  }

  OnActionClick(event: any) {
    let currentElement;
    currentElement = event.currentTarget as HTMLElement;
    console.log(
      'shu on action click',
      this.props.uischema.options?.IsTypeActon ||
        this.props.uischema.options?.IsTypeAction
    );

    // this.fn7Sdk.openDialogRef('asdsada','assdasd')
    const action_type = this.isTypeAction;

    if (!action_type) {
      return;
    }

    // console.log('OnActionClick', this);

    let rootData = this.jsonFormsService.getState().jsonforms.core?.data;
    // console.log(rootData, this.path, get(rootData, this.path))
    // console.log("form Value", this.form.value)
    if (this.path) {
      this.CurretnIterationdata = get(rootData, this.path);
    } else {
      this.CurretnIterationdata = rootData;
    }
    // console.log('data bases path', this.CurretnIterationdata)
    // get current data

    // return 0;
    let options = this.props.uischema.options;
    console.log(this.props);
    console.log(rootData);
    let doc_type: string =
      options.doc_type ||
      this.CurretnIterationdata?.doc_type ||
      this.route.snapshot.params['doc_type'] ||
      null;
    let doc_id: string =
      this.CurretnIterationdata?.doc_id ||
      this.route.snapshot.params['doc_id'] ||
      null;
    let mode: string = options.mode || 'list';
    const jsonFormInstanceKey = !doc_id ? doc_type : `${doc_type}-${doc_id}`;
    // console.log(doc_type, doc_id, mode)
    // check its navigation or functional
    if (this.props.uischema.options?.action_type == 'navigation') {
      let filter: string = this.CurretnIterationdata?.filter;
      if (!doc_type && !doc_id) {
        this.snackBar.openSnackBar('doc_id or doc_type is not found', 'error');
        return;
      }
      if (doc_type && !doc_id) {
        this.router.navigate([`/admin/dynamic/${doc_type}`], {
          queryParams: { filter: filter },
        });
        return;
      }
      if (doc_type && mode == 'list') {
        this.router.navigate([`/admin/dynamic/${doc_type}`], {
          queryParams: { filter: filter },
        });

        return;
      } else {
        this.router.navigate([`/admin/dynamic/${doc_type}/${doc_id}`], {
          queryParams: { filter: filter },
        });
      }
    } else if (this.props.uischema.options?.action_type == 'popup') {
      let config = {
        doc_type: options?.doc_type,
        action_type: options?.type,
        mode: options.mode,
        data: this.jsonFormsService.getState().jsonforms.core?.data,
      };
      console.log(config);
      // const dialogRef = this.dialog.open(DialogReferenceComponent, {
      //   width: '80%',
      //   height: '85%',
      //   data: config,
      // });
    } else {
      // console.log(this)
      if (!this.work) {
        this.work = new WorkflowEngineService(this.fn7Sdk);
      }
      const workFlowInput: WorkFlowInput = {
        doc_type: options?.doc_type,
        doc_type_filter: options?.doc_type_filter,
        fn7_workflow_id: options?.fn7_workflow_id || null,
        data: this.CurretnIterationdata, //this.jsonFormsService.getState().jsonforms.core?.data
        json_form_instance: this.jsonFormsService, // As of now this return current jsonform type context, we should be able to send entire form context
        action: options.workflow_action,
        action_label: options.workflow_action_label,
        jsonFormInstanceKey: '', //jsonFormInstanceKey,
        jsonContextService: this.jsonContext,
        instanceAdditionalData: '',
        //this.jsonContext.getInstanceAdditionalData(jsonFormInstanceKey),
        async_action: false,
        context: null,
        doc_id: this.CurretnIterationdata?.doc_id || null,
        mode: options?.mode || 'view',
        template_name: options?.template_name,
        additional_data: {
          jsonSchemaRef: null,
          uiSchemaRef: null,
          data_filter: '',
        },
        perform_pre_action: false,
        pre_action_config: null,
        htmlCurrentElement: currentElement,
        currentPath: this.propsPath,
        control_uischema: this.uischema,
      };
      console.log('workFlowInput', workFlowInput);
      this.work.executor(workFlowInput, (resp: any) => {
        // console.log("callback result => ", resp, workFlowInput.post_action_config)
      });
    }
  }
}
