import { Component } from '@angular/core';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { JsonFormsAngularService } from '@jsonforms/angular';

@Component({
  selector: 'ssr-radio',
  templateUrl: './radio.component.html',
  styleUrls: ['./radio.component.scss'],
})
export class RadioComponent extends BaseJsonformsAbstractControlComponent {
  radioOptions: Array<any> = [];

  constructor(jsonformsService: JsonFormsAngularService) {
    super(jsonformsService);
  }

  override ngOnInit(): void {
    super.ngOnInit();
    this.radioOptions =
      this.schema.enum || this.uischema?.options?.['radioOptions'] || [];

    // Set the default value to the form control
    if (this.schema?.default && this.form) {
      this.props.data = this.schema.default;
      this.onValueChange(this.props.data);
    }
  }

  override getEventValue = (event: any) => {
    return event;
  };

  onValueChange(event: any) {
    this.onChange(event.value);
    console.log(this.props);
  }
}
