import { Component, Inject, Injector, Input, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar';
// import { JsonFormContextService } from '@app/fn7-ui-renderer';
// import { DialogReferenceComponent } from '@app/standalone-modules';
import { createAjv } from '@jsonforms/core';
// import { LookUpService } from 'libs/fn7-ui-renderer/src/lib/services/look-up.service';
import _ from 'lodash';
import { BehaviorSubject } from 'rxjs';
import { isEmpty } from 'lodash';
import { LookUpService } from '@app/fn7-common-lib';
import { JsonFormsAngularService } from '@jsonforms/angular';

@Component({
  selector: 'ssr-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.css'],
})
export class DialogComponent {
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private injector: Injector,
    // private jsonFormContextService: JsonFormContextService,
    private _snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<DialogComponent>
  ) {
    this.ajv = createAjv({ allErrors: true });
  }
  horizontalPosition: MatSnackBarHorizontalPosition = 'end';
  verticalPosition: MatSnackBarVerticalPosition = 'top';
  ajv: any;
  JsonSchema: any;
  JsonUiSchema: any;
  Jsondata: any = {};

  ChangedData: any = {};
  loading$ = new BehaviorSubject(false);
  args: any;
  additionalData: any = {};
  jsonContext: any;
  @Input() schemaConfig: any;

  ngOnInit() {
    console.log('Dialog', this.data);
    this.getSchemas();
  }

  getSchemas() {
    let config = isEmpty(this.schemaConfig) ? this.data : this.schemaConfig;
    console.log('configaaaaaa', config);
    this.args = {
      doc_type: config.doc_type,
      doc_id: config?.doc_id,
      mode: config.mode,
      additional_data: config.additional_data,
      template_name: config?.template_name,
      filters: config?.filters,
      isMultiSelect: config?.isMultiSelect,
      hasBackdrop: config?.hasBackdrop,
      data: config?.data,
      fetch_schemas: config.schema && config.uiSchema ? false : true,
      fetch_data:
        config.data?.length > 0 || Object.keys(config.data).length > 0
          ? false
          : true,
    };
    this.loading$.next(true);
    // inject look service in closed
    let lookUpService = this.injector.get(LookUpService);
    // const injector = Injector.create({ providers: [], parent: this.injector });
    // const lookUpService = injector.get(LookUpService);

    lookUpService.getSchemasAndData(this.args).subscribe((data: any) => {
      console.log('%c data', 'font-size:20px; color:blue', data);

      let totalRecords;
      if (this.args.fetch_schemas) {
        this.JsonSchema = data?.JsonSchema;
        this.JsonUiSchema = data?.JsonUiSchema;
      } else {
        this.JsonSchema = config.schema;
        this.JsonUiSchema = config.uiSchema;
      }

      if (
        this.args.data?.length > 0 ||
        Object.keys(this.args.data).length > 0
      ) {
        this.Jsondata = this.args.data;
        totalRecords = this.args.data.length;
      } else {
        this.Jsondata = data?.JsonData || {};
      }

      if (this.args.hasBackdrop) {
        this.dialogRef.disableClose = true;
      }

      if (this.args['mode'] == 'list') {
        _.set(this.JsonUiSchema, 'options.show_actions', true);
        _.set(this.JsonUiSchema, 'options.isSelectable', true);
        _.set(this.JsonUiSchema, 'options.hide_addnew', true);
        if (this.args.isMultiSelect) {
          _.set(this.JsonUiSchema, 'options.isMultiSelect', true);
        }

        // updating selectd values with maping field
        // console.log(this.data?.data[this.data.maping_field])
        // this.Jsondata?.map((item: any) => {
        //     this.data.data[this.data.maping_field]?.map((selectedItem: any) => {
        //       if(item.doc_id == selectedItem.doc_id) {
        //         item.checked = true
        //       }
        //     })
        // })
      }
      this.loading$.next(false);
      let additionalData: any = data.additionalData;
      this.additionalData['totalRecords'] = additionalData['totalRecords']
        ? additionalData['totalRecords']
        : totalRecords;
      this.additionalData['doc_type'] = this.JsonSchema.doc_type;
      this.additionalData[
        'jsonFormInstanceKey'
      ] = `Dialog-${this.JsonSchema?.doc_type}`;
      this.additionalData['recordsPerPage'] =
        this.JsonUiSchema.options.recordsPerPage || 10;
      this.additionalData['page'] = 1;
      console.log('this.Jsondata', this.Jsondata, this.additionalData);
      console.log('this.JsonUiSchema', this.JsonUiSchema);
      console.log('this.JsonSchema', this.JsonSchema);
    });
  }
  openSnackBar() {
    this._snackBar.open('Fill Required Fields', 'Dismiss', {
      horizontalPosition: this.horizontalPosition,
      verticalPosition: this.verticalPosition,
      duration: 3000,
    });
  }

  validateData(data: any, schema: any): boolean {
    const validate = this.ajv.compile(schema);

    // console.log(updatedData);

    const valid = validate(data);

    if (!valid) {
      console.error('Validation errors:', validate.errors);
    }

    return valid;
  }

  onDataChange(event: any) {
    console.log('onDataChange', event);
    this.ChangedData = event;
    console.log('onDataChange', this.Jsondata);
    // this.Jsondata = event
  }
  @ViewChild('jsonforms', { static: false }) jsonforms1: any;

  removeInstance() {
    // this.jsonFormContextService.deleteDialog(
    //   this.additionalData['jsonFormInstanceKey']
    // );
  }

  closeDialog(): void {
    const dialogData: any = {
      /* Data you want to pass back to the parent component */
    };
    console.log('dialogData', this.jsonforms1);
    let ChangedData =
      this.jsonforms1.jsonforms1.jsonformsService._state.core.data;
    if (!Array.isArray(ChangedData)) {
      const isValid = this.validateData(ChangedData, this.JsonSchema);
      if (!isValid) {
        this.jsonforms1.jsonforms1.jsonformsService.updateValidationMode(
          'ValidateAndShow'
        );
        this.openSnackBar();
        return;
      }
    }
    console.log('ChangedData', ChangedData);

    // if(this.args.mode == 'list') {
    //   ChangedData = ChangedData.filter((item: any) => item.checked == true)
    // }
    this.dialogRef.close(ChangedData);
    // this.removeInstance();
  }
}
