  getDocument(doc_type: any, doc_id: any) {
    return new Promise((resolve, reject) => {
      this.fn7Sdk.getDocument(doc_type, doc_id).then(
        (res: any) => {
          // console.log("getDocument", res)
          resolve(res);
        },
        (err: any) => {
          // console.log("getDocument", err)
          reject(err);
        }
      );
    });
  }

