  OnActionClick() {
    if (
      !this.props.uischema.options?.IsTypeActon ||
      !this.props.uischema.options?.IsTypeAction
    ) {
      return;
    }

    console.table(this.data);
    const doc_type: string = this.data.doc_type;
    const doc_id: string = this.data.doc_id;
    const filter: string = this.data.filter;

    if (!doc_type || !doc_id) {
      this.snack.openSnackBar('doc_id or doc_type is not found', 'error');
      return;
    }
    this.router.navigate([`/dynamic/${doc_type}/${doc_id}`], {
      queryParams: { filter: filter },
    });
  }

