  onCardClick() {
    if (!this.props.uischema.options?.IsTypeActon) {
      return;
    }

    const options = this.props.uischema.options;
    let CurretnIterationdata: any = null;
    const rootData = this.jsonFormsService.getState().jsonforms.core?.data;

    console.warn(this.jsonFormsService.getState());
    console.warn(this.jsonFormsService);

    // console.log(this.jsonFormsService.getState().jsonforms.core?.data)
    // console.log(this.jsonFormsService.getState().jsonforms.core)
    console.warn(this.jsonFormsService);

    if (this.path) {
      CurretnIterationdata = get(rootData, this.path);
    } else {
      CurretnIterationdata = rootData;
    }

    const work = new WorkflowEngineService(this.fn7Sdk);
    const workFlowInput: WorkFlowInput = {
      doc_type: options?.doc_type,
      doc_type_filter: options?.doc_type_filter,
      fn7_workflow_id: options?.fn7_workflow_id || null,
      data: CurretnIterationdata, //this.jsonFormsService.getState().jsonforms.core?.data
      json_form_instance: this.jsonFormsService, // As of now this return current jsonform type context, we should be able to send entire form context
      action: options.workflow_action || 'getApplicationLogo',
      action_label: options.workflow_action_label,
      async_action: false,
      context: null,
      doc_id: CurretnIterationdata?.doc_id || null,
      mode: options?.mode || 'view',
      template_name: options?.template_name,
      additional_data: {
        jsonSchemaRef: null,
        uiSchemaRef: null,
        data_filter: '',
      },
      perform_pre_action: false,
      pre_action_config: null,
    };
    work.executor(workFlowInput, (resp: any) => {
      // // console.log("callback result => ", resp, workFlowInput.post_action_config)
    });
  }
