  onDeleteClick(item: number) {
    this.removeItems(this.propsPath, [item])();
  }

  getSize(item: any) {
    if (
      !item.options?.size ||
      item.options?.size == undefined ||
      item.options?.size == null
    ) {
      return `width:'fit-content'`;
    }
    let width = 100 / 12;
    let size = `width:${width * Number(item.options?.size) - 1}%`;
    // // console.log(size);

    if (item.options?.size == 6) {
      size = `width:49%`;
    }
    // if (this.display_type == 'edit') {
    //   size = `width:100%`
    // }
    return size;
  }

  getProps: any = _.memoize((index: number): OwnPropsOfRenderer => {
    const uischema = findUISchema(
      this.uischemas,
      this.scopedSchema,
      this.uischema.scope,
      this.propsPath,
      undefined,
      this.uischema,
      this.rootSchema
    );

    this.CurrentArrayIterationItem = {
      schema: this.scopedSchema,
      path: Paths.compose(this.propsPath, `${index}`),
      uischema,
      // data: this.props.data,
    };
    return this.CurrentArrayIterationItem;
  });

