import { Component } from '@angular/core';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';

@Component({
  selector: 'ssr-divider',
  templateUrl: './divider.component.html',
  styleUrls: ['./divider.component.css'],
})
export class DividerComponent extends BaseJsonformsAbstractControlComponent {
  currentLocale: string;
  display_type: string = 'view';
  orientation: boolean = true;

  public override mapAdditionalProps(props: any) {
    this.props = props;
    this.display_type = props.uischema.options.display_type;
    this.orientation =
      props.uischema.options.orientation == 'vertical' ? true : false;
  }
}
