import { Component, OnInit } from '@angular/core';
import { JsonFormsAngularService } from '@jsonforms/angular';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { OwnPropsOfRenderer } from '@jsonforms/core';

@Component({
  selector: 'ssr-flex-layout',
  templateUrl: './flex-layout.component.html',
  styleUrls: ['./flex-layout.component.scss'],
})
export class FlexLayoutComponent
  extends BaseJsonformsAbstractControlComponent
  implements OnInit
{
  display_type: any;

  constructor(
    jsonformsService: JsonFormsAngularService
    // private jsonContext: JsonFormContextService,
    // private cdr: ChangeDetectorRef
  ) {
    super(jsonformsService);
    console.log('fn7 helika constructor FlexLayoutComponent');
  }

  override ngOnInit(): void {
    super.ngOnInit();
    // this.jsonContext.getParentEmitter().subscribe((res: any) => {
    //   if (res && res.cache) {
    //     this.cdr.detectChanges();
    //   }
    // });
  }

  public override mapAdditionalProps(props: any) {
    this.props = props;
    this.display_type = this.jsonFormsService.getState().jsonforms.core
      ?.uischema.options?.['IsUpsert']
      ? 'edit'
      : 'view';
  }

  getProps(index: number, child: any): OwnPropsOfRenderer {
    const result = {
      schema: this.scopedSchema,
      // path: Paths.compose(this.propsPath, `${index}`),
      path: this.propsPath,
      uischema: child,
    };
    return result;
  }

  trackByFn(index: number) {
    return index;
  }
}
