import { DatePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { JsonFormsAngularService } from '@jsonforms/angular';
import { BehaviorSubject } from 'rxjs';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { get } from 'lodash';

@Component({
  selector: 'ssr-label-input',
  templateUrl: './label-input.component.html',
  styleUrls: ['./label-input.component.scss'],
})
export class LabelInputComponent
  extends BaseJsonformsAbstractControlComponent
  implements OnInit
{
  currentLocale: string;
  display_type = 'view';
  dataType = 'string';
  dataTobind = new BehaviorSubject('');
  docTypeValues: any;
  loading = false;

  constructor(
    override jsonFormsService: JsonFormsAngularService,
    // private lookupService: LookUpService,
    // private cdr:ChangeDetectorRef,
    private datePipe: DatePipe
  ) {
    super(jsonFormsService);
  }

  override ngOnInit(): void {
    super.ngOnInit();
    this.display_type = this.jsonFormsService.getState().jsonforms.core
      ?.uischema.options?.['IsUpsert']
      ? 'edit'
      : 'view';
    this.dataType = this.props.data ? typeof this.props.data : 'undefined';
    if (Array.isArray(this.props.data)) {
      this.dataType = 'array';
    }

    // this.dataCalculation(props.data)
    this.getInFormet();
  }

  public override mapAdditionalProps(props: any) {
    this.props = props;
    //   this.display_type = this.jsonFormsService.getState().jsonforms.core?.uischema.options?.['IsUpsert'] ? 'edit' : 'view';
    //   this.dataType = this.props.data ? typeof this.props.data : 'undefined'
    //   if (Array.isArray(this.props.data)) {
    //     this.dataType = 'array'
    //   }
    //   // this.dataCalculation(props.data)
    //   this.getInFormet()
    this.getInFormet();
  }

  removeElementByPath(element: any, path: string): any {
    if (element.scope && element.scope === path) {
      return null;
    }

    if (element.elements) {
      const updatedElements: any[] = [];

      for (const child of element.elements) {
        const updatedChild = this.removeElementByPath(child, path);

        if (updatedChild) {
          const existingElement = updatedElements.find((el: any) =>
            el.scope.startsWith(updatedChild.scope)
          );

          if (!existingElement) {
            updatedElements.push(updatedChild);
          }
        }
      }

      return {
        ...element,
        elements: updatedElements,
      };
    }

    return element;
  }

  isString(item: any): boolean {
    return typeof item === 'string';
  }

  async getInFormet() {
    // console.log('getInFormet called',this.props)
    let item = this.props.data;
    this.loading = true;

    const rootData: any = this.jsonFormsService.getState().jsonforms.core?.data;
    const data_calculation_config =
      this.props.uischema.options?.data_calculation;
    let CurrentPathData;
    if (this.path) {
      CurrentPathData = get(rootData, this.path);
    } else {
      CurrentPathData = rootData;
    }

    if (data_calculation_config?.isRefDoc) {
      const payload: any = {
        doc_type: data_calculation_config?.doc_type,
        additional_data: {
          data_filter: {},
        } as any,
      };

      const params = {
        refParams: data_calculation_config,
        data: CurrentPathData,
      };

      let filter: any;
      if (data_calculation_config?.queryBuilderFn) {
        const queryBuilderFn = new Function(
          'params',
          data_calculation_config?.queryBuilderFn
        );
        filter = queryBuilderFn(params);
        // payload.additional_data.data_filter = filter
      }

      // console.log(filter)

      let data: any;
      // check for previous data
      this.docTypeValues = JSON.parse(
        localStorage.getItem('docTypeValues') || '[]'
      );
      // let found :any= this.docTypeValues?.filter((item: any) => item.doc_type == payload.doc_type)
      // // console.log(found)
      // if (found.length > 0) {
      //     data = found[0].data
      // }else{
      const res: any = {}; //await this.lookupService.getLookUpData(payload);
      this.docTypeValues.push({ doc_type: payload.doc_type, data: res });
      // localStorage.setItem('docTypeValues', JSON.stringify(this.docTypeValues))

      data = res;

      // }
      const filterParts = filter.filter.split(':');
      const filterKey = filterParts[0];
      const filterValue = filterParts[1];

      const filteredData = data.data.filter((item: any) => {
        return item[filterKey] === filterValue;
      });

      data.data = filteredData;

      const fn = new Function(
        'data',
        data_calculation_config.jsonDataTransformFn
      );
      item = fn(data);
      item = Number(item) ? (item % 1 !== 0 ? item.toFixed(2) : item) : 0;
    } else if (data_calculation_config?.jsonDataTransformFn) {
      if (data_calculation_config?.jsonDataTransformFn) {
        const fn = new Function(
          'params',
          data_calculation_config.jsonDataTransformFn
        );
        // console.log(CurrentPathData)
        item = fn(CurrentPathData);
        // item = item ? item % 1 !== 0 ? item.toFixed(2) : item :0
      }
    }

    if (this.props.uischema.options?.datePattern) {
      const datePattern = this.props.uischema.options.datePattern;
      this.data = this.datePipe.transform(new Date(item), datePattern);
      this.dataTobind.next(this.data);
    } else {
      this.dataTobind.next(item);
      this.data = item;
    }

    this.loading = false;
    // this.cdr.detectChanges()
    return this.data;
  }
}
