import {
  ChangeDetectorRef,
  Component,
  Inject,
  PLATFORM_ID,
} from '@angular/core';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { JsonFormsAngularService } from '@jsonforms/angular';
import { Fn7SDKService } from '@app/fn7-common-lib';
import { ActivatedRoute } from '@angular/router';
import { get as lget, join as ljoin } from 'lodash';
import * as echarts from 'echarts';
import * as world from './world.json';
import { isPlatformBrowser } from '@angular/common';

@Component({
  selector: 'ssr-echarts',
  templateUrl: './echarts.component.html',
  styleUrls: ['./echarts.component.css'],
})
export class EchartsComponent extends BaseJsonformsAbstractControlComponent {
  chartOptions: any;
  loading = false;

  constructor(
    override jsonFormsService: JsonFormsAngularService,
    private fn7Sdk: Fn7SDKService,
    private cdr: ChangeDetectorRef,
    private route: ActivatedRoute,
    @Inject(PLATFORM_ID) private platformId: Object
  ) {
    super(jsonFormsService);
  }

  override ngOnInit(): void {
    echarts.registerMap('world', world as any);

    super.ngOnInit();
  }
  public override mapAdditionalProps(props: any) {
    this.loading = true;
    this.props = props;
    if (this.props.uischema.options.useDefaultData) {
      this.loading = false;
      this.chartOptions =
        this.props.data || this.props.uischema.options?.chartOptions;
      return;
    }
    if (this.props.uischema.options?.chartOptions) {
      console.log(this.props.uischema.options?.chartOptions);
      this.chartOptions = this.evaluateFunctions(
        this.props.uischema.options?.chartOptions
      );
      console.log(this.chartOptions);
    }
    if (
      this.props.data &&
      Object.keys(this.props.data).length &&
      this.chartOptions?.series
    ) {
      this.assignDataToCharts();
    }

    if (this.props.uischema.options?.serverSideAction) {
      let listOfFilters: any = [];
      let uiSchemaESFilter = lget(
        this.props.uischema.options,
        'esFilter',
        undefined
      );
      uiSchemaESFilter && listOfFilters.push(uiSchemaESFilter);
      let filter = lget(
        this.route.snapshot.queryParams,
        'filter.filter',
        undefined
      );
      filter && listOfFilters.push(filter);
      let esFilter = lget(
        this.route.snapshot.queryParams,
        'esFilter',
        undefined
      );
      esFilter && listOfFilters.push(esFilter);
      let filterTemp = ljoin(listOfFilters, ' AND ');
      console.log('Echart card es filter ->', filterTemp);

      let queryParams: any = {};
      if (filterTemp) {
        queryParams['filter'] = filterTemp;
      }

      const payload = this.props.uischema.options?.esQuery
        ? { esQuery: this.props.uischema.options?.esQuery }
        : undefined;
      console.log('payload', payload);

      // store in series data
      this.fn7Sdk
        .executeServerSideAction(
          this.props.uischema.options?.serverSideAction,
          payload,
          queryParams
        )
        .then((res) => {
          this.props.data = res.data.data;
          this.assignDataToCharts();
        });
    } else if (this.chartOptions) {
      this.loading = false;
    }
  }
  assignDataToCharts = () => {
    if (Array.isArray(this.chartOptions?.series)) {
      if (this.chartOptions?.series[0].type == 'sankey') {
        this.chartOptions.series[0].links = this.props.data.links;
        this.props.data = this.props.data.nodes;
      }
      console.log('is array', this.props.data);
      const updatedSeries = this.chartOptions.series.map(
        (series: any, index: any) => {
          series.data = this.props.data[index];
          return series;
        }
      );
      // Assign the new array back to this.chartOptions.series
      this.chartOptions.series = updatedSeries;
    } else {
      if (this.chartOptions?.series?.type == 'sankey') {
        this.chartOptions.series.links = this.props.data.links;
        this.props.data = this.props.data.nodes;
        this.chartOptions.series.data = this.props.data;
      } else {
        this.chartOptions.series.data = this.props.data;
      }
    }
    this.chartOptions = Object.assign({}, this.chartOptions);
    this.loading = false;
    console.log('chart Options after data', this.chartOptions);
    this.cdr.detectChanges();
  };

  override getEventValue = (event: any) => {
    return event;
  };

  evaluateFunctions(obj: any): any {
    let newObj: any;

    if (Array.isArray(obj)) {
      // If obj is an array, map over its elements and evaluate each one
      newObj = obj.map((element: any) => {
        if (typeof element === 'object') {
          return this.evaluateFunctions(element);
        } else if (typeof element === 'string') {
          const match = element.match(/\(([^)]*)\)\s*=>/);
          if (match) {
            try {
              const evaluatedFunction = eval(`(${element})`);
              return evaluatedFunction;
            } catch (error) {
              console.error(`Error evaluating function: ${error}`);
            }
          } else {
            return element;
          }
        } else {
          return element;
        }
      });
    } else if (typeof obj === 'object') {
      // If obj is an object, recursively evaluate its properties
      newObj = {};
      for (const key in obj) {
        if (typeof obj[key] === 'object') {
          newObj[key] = this.evaluateFunctions(obj[key]);
        } else if (typeof obj[key] === 'string') {
          const match = obj[key].match(/\(([^)]*)\)\s*=>/);
          if (match) {
            try {
              const evaluatedFunction = eval(`(${obj[key]})`);
              newObj[key] = evaluatedFunction;
            } catch (error) {
              console.error(`Error evaluating function in ${key}: ${error}`);
            }
          } else {
            newObj[key] = obj[key];
          }
        } else {
          newObj[key] = obj[key];
        }
      }
    } else {
      // For non-object and non-array types, copy the value
      newObj = obj;
    }

    return newObj;
  }

  get isBrowser() {
    if (isPlatformBrowser(this.platformId)) {
      return true;
    } else {
      return false;
    }
  }
}
