import { Component } from '@angular/core';
import { BaseJsonformsAbstractControlComponent } from '../../shared/base-jsonforms-abstract-control/base-jsonforms-abstract-control.component';
import { SafeHtml, DomSanitizer } from '@angular/platform-browser';
import { JsonFormsAngularService } from '@jsonforms/angular';

@Component({
  selector: 'ssr-rich-text',
  templateUrl: './rich-text.component.html',
  styleUrls: ['./rich-text.component.scss'],
})
export class RichTextComponent extends BaseJsonformsAbstractControlComponent {
  sanitizedHtml: SafeHtml;
  constructor(
    jsonformsService: JsonFormsAngularService,
    private sanitizer: DomSanitizer
  ) {
    super(jsonformsService);
  }

  override ngOnInit(): void {
    super.ngOnInit();

    this.sanitizedHtml = this.sanitizer.bypassSecurityTrustHtml(this.data);

    console.log('RichTextComponent', this.markdownToHTML(this.data));
    console.log('RichTextComponent', this.props);
  }

  get convertToSanitizedHtml() {
    return (this.sanitizedHtml = this.sanitizer.bypassSecurityTrustHtml(
      this.props.uischema.options?.markdown
        ? this.markdownToHTML(this.data)
        : this.data
    ));
  }

  markdownToHTML(src: string): string {
    const rx_lt = /</g;
    const rx_gt = />/g;
    const rx_space = /\t|\r|\uf8ff/g;
    const rx_escape = /\\([\\\|`*_{}\[\]()#+\-~])/g;
    const rx_hr = /^([*\-=_] *){3,}$/gm;
    const rx_blockquote = /\n *&gt; *([^]*?)(?=(\n|$){2})/g;
    const rx_list =
      /\n( *)(?:[*\-+]|((\d+)|([a-z])|[A-Z])[.)]) +([^]*?)(?=(\n|$){2})/g;
    const rx_listjoin = /<\/(ol|ul)>\n\n<\1>/g;
    const rx_highlight =
      /(^|[^A-Za-z\d\\])(([*_])|(~)|(\^)|(--)|(\+\+)|`)(\2?)([^<]*?)\2\8(?!\2)(?=\W|_|$)/g;
    const rx_code = /\n((```|~~~).*\n?([^]*?)\n?\2|((.*?\n)+))/g;
    const rx_link =
      /((!?)\[(.*?)\]\((.*?)( ".*")?\)|\\([\\`*_{}\[\]()#+\-.!~]))/g;
    const rx_table = /\n(( *\|.*?\| *\n)+)/g;
    const rx_thead = /^.*\n( *\|( *\:?-+\:?-+\:? *\|)* *\n|)/;
    const rx_row = /.*\n/g;
    const rx_cell = /\||(.*?[^\\])\|/g;
    const rx_heading = /(?=^|>|\n)([>\s]*?)(#{1,6}) (.*?)( #*)? *(?=\n|$)/g;
    const rx_para = /(?=^|>|\n)\s*\n+([^<]+?)\n+\s*(?=\n|<|$)/g;
    const rx_stash = /-\d+\uf8ff/g;

    function replace(rex: RegExp, fn: any) {
      src = src.replace(rex, fn);
    }

    function element(tag: string, content: string, className: string = '') {
      return `<${tag} class="${className}">${content}</${tag}>`;
    }

    function blockquote(src: string): any {
      return src.replace(
        rx_blockquote,
        function (all: string, content: string) {
          return element(
            'blockquote',
            blockquote(highlight(content.replace(/^ *&gt; */gm, ''))),
            'm-5'
          );
        }
      );
    }

    function list(src: string) {
      return src.replace(
        rx_list,
        function (
          all: string,
          ind: string,
          ol: string,
          num: string,
          low: string,
          content: string
        ) {
          const entry = element(
            'li',
            highlight(
              content
                .split(
                  RegExp(
                    '\n ?' + ind + '(?:(?:\\d+|[a-zA-Z])[.)]|[*\\-+]) +',
                    'g'
                  )
                )
                .map(list)
                .join('</li><li>')
            )
          );

          return (
            '\n' +
            (ol
              ? `<ol class="${
                  ol
                    ? 'list-decimal list-inside pl-2'
                    : 'list-disc list-inside pl-2'
                }" start="${
                  num
                    ? ol + '">'
                    : parseInt(ol, 36) -
                      9 +
                      '" style="list-style-type:' +
                      (low ? 'low' : 'upp') +
                      'er-alpha">'
                }${entry}</ol>`
              : element('ul', entry, 'list-disc list-inside pl-2'))
          );
        }
      );
    }

    function highlight(src: string): any {
      return src.replace(
        rx_highlight,
        function (
          all: string,
          _: string,
          p1: string,
          emp: string,
          sub: string,
          sup: string,
          small: string,
          big: string,
          p2: string,
          content: string
        ) {
          return (
            _ +
            element(
              emp
                ? p2
                  ? 'strong'
                  : 'em'
                : sub
                ? p2
                  ? 's'
                  : 'sub'
                : sup
                ? 'sup'
                : small
                ? 'small'
                : big
                ? 'big'
                : 'code',
              highlight(content)
            )
          );
        }
      );
    }

    function unesc(str: string) {
      return str.replace(rx_escape, '$1');
    }

    const stash: string[] = [];
    let si: number = 0;

    src = '\n' + src + '\n';

    replace(rx_lt, '&lt;');
    replace(rx_gt, '&gt;');
    replace(rx_space, ' ');

    // blockquote
    src = blockquote(src);

    // horizontal rule
    replace(rx_hr, '<hr/>');

    // list
    src = list(src);
    replace(rx_listjoin, '');

    // code
    replace(
      rx_code,
      function (all: string, p1: string, p2: string, p3: string, p4: string) {
        stash[--si] = element(
          'pre',
          element('code', p3 || p4.replace(/^/gm, '')),
          'p-2 bg-black rounded text-white'
        );
        return si + '\uf8ff';
      }
    );

    // link or image
    replace(
      rx_link,
      function (
        all: string,
        p1: string,
        p2: string,
        p3: string,
        p4: string,
        p5: string,
        p6: string
      ) {
        stash[--si] = p4
          ? p2
            ? `<img src="${p4}" alt="${p3}"/>`
            : `<a href="${p4}">${unesc(highlight(p3))}</a>`
          : p6;
        return si + '\uf8ff';
      }
    );

    // table
    replace(rx_table, function (all: string, table: string) {
      const sep = table.match(rx_thead)![1];
      return (
        '\n' +
        element(
          'table',
          table.replace(rx_row, function (row: string, ri: number) {
            return row == sep
              ? ''
              : element(
                  'tr',
                  row.replace(
                    rx_cell,
                    function (all: string, cell: string, ci: number) {
                      return ci
                        ? element(
                            sep && !ri ? 'th' : 'td',
                            unesc(highlight(cell || ''))
                          )
                        : '';
                    }
                  )
                );
          })
        )
      );
    });

    // heading
    replace(
      rx_heading,
      function (all: string, _: string, p1: string, p2: string) {
        return (
          _ +
          element(
            'h' + p1.length,
            unesc(highlight(p2)),
            `text-${5 - p1.length}xl font-bold`
          )
        );
      }
    );

    // paragraph
    replace(rx_para, function (all: string, content: string) {
      return element('p', unesc(highlight(content)), 'text-base');
    });

    // stash
    replace(rx_stash, function (all: string) {
      return stash[parseInt(all)];
    });

    return src.trim();
  }
}
