import {
  ChangeDetectorRef,
  Component,
  Inject,
  Input,
  PLATFORM_ID,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SsrFn7UiRendererModule } from '../ssr-fn7-ui-renderer.module';
import { MaterialModule } from '@app/material';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Subject, BehaviorSubject, filter, takeUntil } from 'rxjs';
import { ActivatedRoute, Router, NavigationStart } from '@angular/router';
import { cloneDeep } from 'lodash';
interface TriggerDataCallOnCachedRoute {
  doc_type: string;
  data$: any;
}

@Component({
  selector: 'ssr-helika-renderer',
  standalone: true,
  imports: [CommonModule, SsrFn7UiRendererModule],
  templateUrl: './helika-renderer.component.html',
  styleUrls: ['./helika-renderer.component.scss'],
})
export class HelikaRendererComponent {
  @Input() jsonSchema: any;
  @Input() jsonUISchema: any;
  @Input() data: any;
  routePath: string;
  destroyed$ = new Subject<boolean>();
  loading$ = new BehaviorSubject(false);
  triggerDataCallOnCachedRouteSubject =
    new BehaviorSubject<TriggerDataCallOnCachedRoute>({
      doc_type: '',
      data$: null,
    });
  additionalData: any = {};
  isServer = true;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private changeDetectorRef: ChangeDetectorRef,
    @Inject(PLATFORM_ID) private platformId: Object
  ) {
    this.router.events
      .pipe(filter((event) => event instanceof NavigationStart))
      .subscribe(() => {
        this.loading$.next(true);
        this.changeDetectorRef.markForCheck();
      });

    this.route.data.pipe(takeUntil(this.destroyed$)).subscribe((data) => {
      console.warn(
        'in admin-cube DynamicRenderComponent route.data.subscribe',
        data
      );
      this.handleRouteData(data);
    });
  }

  ngOnInit(): void {
    console.warn('in admin-cube DynamicRenderComponent ngOnInit');
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  private handleRouteData(data: any): void {
    if (this.isServer) {
      return this.handleNewRouteData(data.data);
    }
    if (data.data.isCachedRoute) {
      this.handleCachedRouteData(data.data);
    } else {
      this.handleNewRouteData(data.data);
    }
    this.changeDetectorRef.markForCheck();
  }

  private handleCachedRouteData(data: any): void {
    this.triggerDataCallOnCachedRouteSubject.next(data);
    this.loading$.next(false);
  }

  private handleNewRouteData(data: any): void {
    this.routePath = data.routePath;
    this.additionalData = this.constructAdditionalData(data);

    this.jsonSchema = cloneDeep(data.JsonSchema);
    this.jsonUISchema = cloneDeep(data.JsonUiSchema);
    this.data = cloneDeep(data.JsonData);
    this.loading$.next(false);
  }

  private constructAdditionalData(data: any): any {
    const additionalData = data.additionalData || {};
    additionalData.doc_id = data.doc_id;
    additionalData.doc_type = data.doc_type;
    additionalData.IsParent = true;
    additionalData.routePath = this.routePath;
    additionalData.jsonFormInstanceKey =
      this.route.snapshot.queryParams['type'] === 'list'
        ? data.doc_type
        : `${data.doc_type}-${data.doc_id}`;

    return additionalData;
  }

  private removeIdFromSchema(schema: any): any {
    delete schema?.$id;
    return schema;
  }
}
